<template>
  <div class="head">
    <van-nav-bar v-if="isShowTop" class="top" :class="[{'active': !isSubHeader}]" :title="title" left-text="">
      <template #left>
        <img class="back" src="@/assets/images/back@2x.png" @click="$goBack" alt="">
        <img v-if="isClose" class="back" src="@/assets/images/closer@2x.png" @click="closeWeb" alt="">
      </template>
      <template #right>
        <img class="more-img" src="@/assets/images/more.png" @click="onMenu" alt=""> 
      </template>
    </van-nav-bar>
    <van-nav-bar v-if="isSubHeader" left-text="" :class="[{'scroll': headerActive},{'bottom':isShowTop && isSubHeader}]" left-arrow>
      <template #left>
        <img class="logo" src="@/assets/images/logo.png" alt="">
      </template>
      <template #title>
        <span>首页</span>
      </template>
      <template #right>
        <img class="more-img" src="@/assets/images/more.png" @click="onMenu" alt=""> 
      </template>
    </van-nav-bar>
    <!-- 菜单栏 -->
    <div class="menu-Block" :class="[{'open':open},{'isSubHeader':isShowTop && isSubHeader}]" @click.self="closeMenu()">
      <div class="menu-leftBlock">
        <div class="address-status">
          <div class="address-text">地址</div>
          <div class="top" style="margin-bottom: 10px">
            <div class="left ">{{address}}</div>
            <div class="right"><img class="copy clip-menu-button" :data-clipboard-text="address" src="@/assets/images/copy-default@2x.png" alt=""></div>
          </div>
           <div class="address-text">推广链接</div>
          <div class="top">
            <div class="left ">{{link}}</div>
            <div class="right"><img class="copy clip-menu-button" :data-clipboard-text="link" src="@/assets/images/copy-default@2x.png" alt=""></div>
          </div>
        </div>
        <div class="menu-item flex-between" @click="$router.push({name: 'Index'})">
          <div class="left flex-middle">
            <div class="menu-tit">首页</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>
        
        <div class="menu-item flex-between" @click="$router.push({name: 'Swap'})">
          <div class="left flex-middle">
            <div class="menu-tit">兑换</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>
        
         <div class="menu-item flex-between" @click="$router.push({name: 'Merge'})">
          <div class="left flex-middle">
            <div class="menu-tit">合成</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>

        <div class="menu-item flex-between" @click="$router.push({name: 'User'})">
          <div class="left flex-middle">
            <div class="menu-tit">我的资产</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useRouter, onBeforeRouteLeave } from 'vue-router'
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
import { Toast } from 'vant';
import Clipboard from 'clipboard'
// import config from "@/utils/config";
export default {
  components: {
  },
  props: {
    Headtype: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    isSubHeader: {
      type: Boolean,
      default: false
    },
    isShowTop: {
      type: Boolean,
      default: true
    },
    isClose: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const store = useStore()
    const router = useRouter()
    // const route = useRoute
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      open: false,
      Headtype: props.Headtype,
      headerActive: 0,
      title: props.title,
      local: getLang().toLowerCase() || "tw",
      isSubHeader: props.isSubHeader,
      showLang: false,
      isActive: false,
      link: '',
      langList: [
        { text: '繁體', lang: 'tw' },
        { text: 'English', lang: 'en' }
      ],
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.link = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
      },
      initWallet: () => {
        web3Data.initWeb3()
      },
      onMenu: () => {
        let flag = !data.open
        data.open = flag
        document.querySelector('body').style.overflow = flag ? 'hidden' : 'auto'
      },
      closeWeb: () => {
        router.replace({ name: 'Index' })
        document.querySelector('body').style.overflow = 'auto'
        data.open = false
      },
      closeMenu: () => {
        document.querySelector('body').style.overflow = 'auto'
        data.open = false
      }
    })
    onMounted(() => {
      var clipboard = new Clipboard('.clip-menu-button')
      clipboard.on('success', function (e) {
        Toast('复制成功')
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast('复制失败，请手动复制')
      })
    })
    document.addEventListener('scroll', function () {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    })
    if (data.address) {
      data.initContract()
    }
    const onSelectLang = (action) => {
      if (action.lang) {
        data.changeLanuage(action.lang)
      }
      data.showLang = false
    }
    const IndexHandle = () => {
      if (router.currentRoute['_value'].name === 'Index') {
        data.open = false
        document.querySelector('body').style.overflow = 'auto'
      } else {
        router.push({ name: 'Index', query: {} })
      }
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    onBeforeRouteLeave(() => {
      document.querySelector('body').style.overflow = 'auto'
      data.open = false
    })
    const refData = toRefs(data);
    return {
      ...refData,
      onSelectLang,
      IndexHandle
    };
  }
}
</script>
<style lang="less" scoped>
.logo{
  filter: brightness(100000%);
}
.address-text{
  font-size: 15px;
  color: #fff;
  font-weight: 600;
  margin-bottom: 5px;
}
.wallet-block {
  border: 1px solid #ffffff;
  border-radius: 20px;
  padding: 4px 10px;
  width: 108px;
  .wallet-img {
    width: 19px;
    height: 19px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .address {
    overflow: hidden;
    white-space: nowrap;
    font-size: 14px;
    color: #ffffff;
    margin-left: 10px;
  }
}
.menu-Block {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  z-index: -1;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  &.isSubHeader {
    top: 44px;
    .menu-leftBlock {
      top: 44px;
    }
  }
  &.open {
    opacity: 1;
    z-index: 600;
    .menu-leftBlock {
      opacity: 1;
      left: 0;
    }
  }
  .menu-leftBlock {
    transition: all 0.3s ease-in-out;
    position: fixed;
    width: 75%;
    left: -100%;
    top: 0;
    bottom: 0;
    opacity: 0;
    background: #3F2E6F;
    .menu-item {
      padding: 14px 16px;
      .menu-img {
        width: 22px;
        height: 22px;
      }
      .menu-tit {
        font-size: 14px;
        color: #fff;
        margin-left: 8px;
        font-weight: 700;
      }
    }
  }
}
.arrow {
  width: 6px;
  height: 10px;
  display: block;
  filter: brightness(10000%);
}
.logo {
  width: 36px;
}
.address-status {
  background: rgba(0, 0, 0, 0.15);
  color: #fff;
  font-size: 12px;
  word-break: break-all;
  border-radius: 12px;
  padding: 16px 16px 13px;
  margin: 12px 16px;
  .copy {
    width: 17px;
    flex-shrink: 0;
    margin-left: 20px;
  }
  .top {
    display: flex;
    align-items: center;
  }
  .bottom {
    display: flex;
    align-items: center;
    font-size: 12px;
    padding-top: 12px;
    .img-wrap {
      width: 12px;
      height: 12px;
      margin-right: 4px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .text {
      line-height: 1;
    }
  }
}
.select {
  padding: 6px 0;
  .option {
    padding: 6px 16px;
    &:active {
      opacity: 0.5;
    }
    &.active {
      color: @theme-color;
    }
  }
}
</style>
<style lang="less">
.lang-wrap {
  .van-popover__arrow {
    left: initial !important;
    right: 25px !important;
  }
  &.van-popover {
    left: initial !important;
    right: 0 !important;
  }
}
</style>
